import React, { useState, useEffect } from 'react';
import Camera, { FACING_MODES } from 'react-html5-camera-photo';
import 'react-html5-camera-photo/build/css/index.css';

import "./App.scss";
import Button from './components/Button/Button';
import Gallery from './components/Gallery/Gallery';
import ErrorScreen from './components/ErrorScreen/ErrorScreen';
 
function App (props) {

  const initialError = {
    hasError: false,
    errorMessage: ""
  }

  const [error, setError] = useState(initialError);
  const [photos, setPhotos] = useState([]);

  useEffect(() => {
    console.log(error);
  }, [error])

  useEffect(() => {
    console.log(photos)
  }, [photos])
  
  const handleTakePhoto = (dataUri) => {
    console.log('Foto tirada');
    console.log(dataUri);
    const _photo = {
      photo: dataUri,
      id: photos.length
    }
    setPhotos([
      ...photos,
      {
        photo: _photo.photo,
        id: _photo.id
      }
    ]);
  }

  const handleError = (cameraError) => {
    console.log(cameraError);
    !error.hasError && setError({
      hasError: true,
      errorMessage: "Você precisa garantir permissão de acesso à câmera para continuar."
    });
  }

  const deletePhoto = id => {
    console.log("Entrou no delete")
    console.log(id)
    const _photos = photos.filter(photo => photo.id !== id)
    console.log(_photos)
    setPhotos(_photos)
    // setPhotos({
    //   _photos
    // })
  }
 
  return (
    <div className="app">
      <ErrorScreen error={error} />
      <Camera
        onCameraStart={console.log("STATUS: Camera iniciada")}
        isFullscreen={true}
        onCameraError={(cameraError) => {handleError(cameraError)}}
        onTakePhoto={(dataUri) => {handleTakePhoto(dataUri)}}
        sizeFactor={1}
        idealFacingMode = {FACING_MODES.ENVIRONMENT}
        isImageMirror = {false}
      />
      <Gallery 
        photos={photos} 
        deletePhoto={deletePhoto}
      />
      <Button photosLength={photos.length} />
    </div>
  );
}

export default App;
