import React from "react";

import "./Gallery.scss";

const Gallery = props => {

  const { photos, deletePhoto } = props;

  return (
    photos.length > 0 && (
      <div className="gallery">
        {photos.map((photo, id) => (
          <div className="photoContainer" key={id}>
            <div className="closeButton" onClick={() => deletePhoto(id)}>
              <span className="closeArrow" />
              <span className="closeArrow" />
            </div>
            <img 
              src={photo.photo} 
              alt="thumbnail preview"
            />
          </div>
        ))}
      </div>
    )
  );
}

export default Gallery;
