import React from 'react';

import "./Button.scss";

const Button = props => {

  const { photosLength } = props;

  return (
    photosLength > 0 ? (
      <button className="finishButton">Concluir fotos</button>
    ) : (
      <button className="returnButton">Voltar para Recex</button>
    )
  );
}

export default Button;
